"use client";

import { useEffect, useMemo, useState } from "react";
import BrMap from "@/components/BrMap";

type SeriesPoint = { date: string; value: number | null };
type RegimePoint = { date: string; regime: string; confidence: number };
type RqaInfo = { rqa?: { det?: number; lam?: number; tt?: number } };
type ForecastByRegime = Record<string, { by_regime: Record<string, unknown> }>;

type CityEntry = {
  asset: string;
  state: string;
  city: string;
  region: string;
};

const palette: Record<string, string> = {
  STABLE: "#34d399",
  TRANSITION: "#fbbf24",
  UNSTABLE: "#fb7185",
};

const REGION_ORDER = ["Norte", "Nordeste", "Centro-Oeste", "Sudeste", "Sul"];

const REGION_STATES: Record<string, string[]> = {
  Norte: ["AC", "AP", "AM", "PA", "RO", "RR", "TO"],
  Nordeste: ["AL", "BA", "CE", "MA", "PB", "PE", "PI", "RN", "SE"],
  "Centro-Oeste": ["DF", "GO", "MS", "MT"],
  Sudeste: ["ES", "MG", "RJ", "SP"],
  Sul: ["PR", "RS", "SC"],
};

const STATE_TO_REGION: Record<string, string> = Object.fromEntries(
  Object.entries(REGION_STATES).flatMap(([region, states]) => states.map((uf) => [uf, region]))
);

const CITY_MAP: CityEntry[] = [
  { asset: "FipeZap_São_Paulo_Total", state: "SP", city: "Sao Paulo", region: "Sudeste" },
  { asset: "FipeZap_Rio_de_Janeiro_Total", state: "RJ", city: "Rio de Janeiro", region: "Sudeste" },
  { asset: "FipeZap_Belo_Horizonte_Total", state: "MG", city: "Belo Horizonte", region: "Sudeste" },
  { asset: "FipeZap_Brasília_Total", state: "DF", city: "Brasilia", region: "Centro-Oeste" },
  { asset: "FipeZap_Porto_Alegre_Total", state: "RS", city: "Porto Alegre", region: "Sul" },
];

// TODO: Expand this list with complete city coverage per UF when real-estate feeds are added.
const CITY_MOCK_BY_UF: Record<string, string[]> = {
  SP: ["Sao Paulo"],
  RJ: ["Rio de Janeiro"],
  MG: ["Belo Horizonte"],
  DF: ["Brasilia"],
  RS: ["Porto Alegre"],
};

function getCitiesForState(uf: string) {
  const fromAssets = CITY_MAP.filter((c) => c.state === uf).map((c) => c.city);
  const fromMock = CITY_MOCK_BY_UF[uf] || [];
  return Array.from(new Set([...fromAssets, ...fromMock]));
}

function formatPct(v?: number) {
  if (v == null || Number.isNaN(v)) return "--";
  return `${(v * 100).toFixed(2)}%`;
}

function formatDateLabel(date: string, mode: "mensal" | "anual" | "diario") {
  if (!date) return "";
  if (mode === "anual") return date.slice(0, 4);
  if (mode === "mensal") return date.slice(0, 7);
  return date;
}

function toTitleRegime(regime?: string) {
  if (!regime) return "--";
  if (regime === "STABLE") return "Estavel";
  if (regime === "TRANSITION") return "Transicao";
  if (regime === "UNSTABLE") return "Instavel";
  return regime;
}

export default function RealEstateDashboard() {
  const [summary, setSummary] = useState<Record<string, unknown> | null>(null);
  const [regionFilter, setRegionFilter] = useState("Sudeste");
  const [stateFilter, setStateFilter] = useState("SP");
  const [cityFilter, setCityFilter] = useState("Sao Paulo");
  const [horizonDays, setHorizonDays] = useState<1 | 5 | 10>(5);
  const [series, setSeries] = useState<SeriesPoint[]>([]);
  const [regimes, setRegimes] = useState<RegimePoint[]>([]);
  const [viewMode, setViewMode] = useState<"mensal" | "anual" | "diario">("mensal");
  const [hoverIndex, setHoverIndex] = useState<number | null>(null);

  const selectedAsset = useMemo(() => {
    const safeCity = getCitiesForState(stateFilter).includes(cityFilter)
      ? cityFilter
      : getCitiesForState(stateFilter)[0] || "";
    const match = CITY_MAP.find((c) => c.state === stateFilter && c.city === safeCity);
    return match?.asset || CITY_MAP[0].asset;
  }, [stateFilter, cityFilter]);

  useEffect(() => {
    const load = async () => {
      try {
        const res = await fetch("/api/realestate/summary");
        if (!res.ok) {
          setSummary(null);
          return;
        }
        setSummary(await res.json());
      } catch {
        setSummary(null);
      }
    };
    load();
  }, []);

  useEffect(() => {
    const loadSeries = async () => {
      try {
        const res = await fetch(`/api/realestate/series?asset=${encodeURIComponent(selectedAsset)}`);
        const data = await res.json();
        setSeries(Array.isArray(data) ? data : []);
      } catch {
        setSeries([]);
      }
    };
    loadSeries();
  }, [selectedAsset]);

  useEffect(() => {
    const loadRegimes = async () => {
      try {
        const base = selectedAsset.toUpperCase().replace(/\s+/g, "_");
        const res = await fetch(`/api/files/realestate/assets/${base}_monthly_regimes.csv`);
        if (!res.ok) {
          setRegimes([]);
          return;
        }
        const text = await res.text();
        const parsed = text
          .trim()
          .split("\n")
          .slice(1)
          .map((line) => {
            const [date, regime, confidence] = line.split(",");
            return { date, regime, confidence: Number(confidence) };
          });
        setRegimes(parsed);
      } catch {
        setRegimes([]);
      }
    };
    loadRegimes();
  }, [selectedAsset]);

  const derivedRegimes = useMemo(() => {
    if (!series.length) return [] as RegimePoint[];
    const diffs = series.map((p, i) => {
      if (i === 0 || p.value == null || series[i - 1].value == null) return 0;
      return Math.abs(((p.value as number) - (series[i - 1].value as number)) / Math.max(1e-9, series[i - 1].value as number));
    });
    const sorted = [...diffs].sort((a, b) => a - b);
    const q = (k: number) => sorted[Math.floor((sorted.length - 1) * k)] || 0;
    const q1 = q(0.35);
    const q2 = q(0.75);
    return series.map((p, i) => {
      const v = diffs[i] ?? 0;
      let regime = "STABLE";
      if (v >= q2) regime = "UNSTABLE";
      else if (v >= q1) regime = "TRANSITION";
      const confidence = Math.max(0.3, Math.min(0.95, regime === "STABLE" ? 0.75 : regime === "TRANSITION" ? 0.58 : 0.42));
      return { date: p.date, regime, confidence };
    });
  }, [series]);

  const activeRegimes = regimes.length ? regimes : derivedRegimes;
  const rqaMap = (summary?.rqa ?? {}) as Record<string, RqaInfo>;
  const forecastMap = (summary?.forecast ?? {}) as Record<string, ForecastByRegime>;
  const rqa: RqaInfo | null = rqaMap[selectedAsset.toUpperCase()] || null;
  const forecast: ForecastByRegime | null = forecastMap[selectedAsset.toUpperCase()] || null;

  const displaySeries = useMemo(() => {
    if (!series.length) return [];
    if (viewMode === "diario") return series;
    if (viewMode === "anual") {
      const byYear: Record<string, SeriesPoint> = {};
      for (const p of series) byYear[p.date.slice(0, 4)] = p;
      return Object.values(byYear);
    }
    return series;
  }, [series, viewMode]);

  const chart = useMemo(() => {
    if (!displaySeries.length) return null;
    const width = 1400;
    const height = 780;
    const pad = 90;
    const yVals = displaySeries.map((d) => d.value).filter((v) => v != null) as number[];
    if (!yVals.length) return null;
    const ymin = Math.min(...yVals);
    const ymax = Math.max(...yVals);
    const scaleX = (i: number, total: number) => pad + (i / Math.max(1, total - 1)) * (width - pad * 2);
    const scaleY = (v: number) => height - pad - ((v - ymin) / Math.max(1e-6, ymax - ymin)) * (height - pad * 2);
    return { width, height, pad, scaleX, scaleY, ymin, ymax };
  }, [displaySeries]);

  const availableStates = REGION_STATES[regionFilter] || [];
  const citiesForState = useMemo(() => getCitiesForState(stateFilter), [stateFilter]);
  const cityFilterValue = citiesForState.includes(cityFilter) ? cityFilter : citiesForState[0] || "";

  const regimeAt = (idx: number | null) => {
    if (idx == null || !activeRegimes.length || !displaySeries.length) return null;
    const mapped = Math.round((idx / Math.max(1, displaySeries.length - 1)) * (activeRegimes.length - 1));
    return activeRegimes[Math.max(0, Math.min(activeRegimes.length - 1, mapped))] || null;
  };

  const latestValue = displaySeries.length ? displaySeries[displaySeries.length - 1].value : null;
  const regimeNow = activeRegimes.length ? activeRegimes[activeRegimes.length - 1] : null;
  const periodInfo = displaySeries.length
    ? `${displaySeries[0].date} ate ${displaySeries[displaySeries.length - 1].date}`
    : "Sem periodo";

  const forecastValue = useMemo(() => {
    if (!latestValue || !Number.isFinite(latestValue)) return null;
    const baseConfidence = regimeNow?.confidence ?? 0.55;
    const drift = regimeNow?.regime === "STABLE" ? 0.005 : regimeNow?.regime === "TRANSITION" ? 0.002 : -0.004;
    const factor = 1 + drift * horizonDays;
    return {
      p50: latestValue * factor,
      confidence: Math.max(0.3, Math.min(0.95, baseConfidence - (horizonDays - 1) * 0.03)),
    };
  }, [latestValue, regimeNow, horizonDays]);

  const onSelectUF = (uf: string) => {
    const nextRegion = STATE_TO_REGION[uf] || regionFilter;
    setRegionFilter(nextRegion);
    setStateFilter(uf);
  };

  return (
    <div className="p-6 space-y-6">
      <header className="space-y-3">
        <h1 className="text-2xl font-semibold">Setor Imobiliario</h1>
        <p className="text-sm text-zinc-400">
          Diagnostico de regimes para precos residenciais com leitura de estabilidade, transicao e
          instabilidade.
        </p>
      </header>

      <div className="grid grid-cols-12 gap-6">
        <div className="col-span-12 lg:col-span-4 space-y-4">
          <div className="rounded-xl border border-zinc-800 bg-black/40 p-4">
            <div className="text-xs text-zinc-500 mb-2">Filtro geografico</div>
            <div className="space-y-3">
              <label className="text-xs text-zinc-400">Regiao</label>
              <select
                className="w-full rounded-lg border border-zinc-700 bg-black/30 px-3 py-2 text-sm"
                value={regionFilter}
                onChange={(e) => {
                  const reg = e.target.value;
                  const firstState = REGION_STATES[reg]?.[0] || "SP";
                  setRegionFilter(reg);
                  setStateFilter(firstState);
                }}
              >
                {REGION_ORDER.map((r) => (
                  <option key={r} value={r}>
                    {r}
                  </option>
                ))}
              </select>

              <label className="text-xs text-zinc-400">Estado</label>
              <select
                className="w-full rounded-lg border border-zinc-700 bg-black/30 px-3 py-2 text-sm"
                value={stateFilter}
                onChange={(e) => setStateFilter(e.target.value)}
              >
                {availableStates.map((s) => (
                  <option key={s} value={s}>
                    {s}
                  </option>
                ))}
              </select>

              <label className="text-xs text-zinc-400">Cidade</label>
              <select
                className="w-full rounded-lg border border-zinc-700 bg-black/30 px-3 py-2 text-sm"
                value={cityFilterValue}
                onChange={(e) => setCityFilter(e.target.value)}
              >
                {citiesForState.map((c) => (
                  <option key={c} value={c}>
                    {c}
                  </option>
                ))}
              </select>
            </div>
          </div>

          <div className="rounded-xl border border-zinc-800 bg-black/40 p-4">
            <div className="text-xs text-zinc-500 mb-2">Mapa politico do Brasil por estados</div>
            <BrMap selectedUF={stateFilter} onSelectUF={onSelectUF} />
            <div className="mt-2 text-xs text-zinc-400">
              Clique no estado para sincronizar o filtro e atualizar a cidade.
            </div>
          </div>

          <div className="rounded-xl border border-zinc-800 bg-black/40 p-4 grid grid-cols-3 gap-3 text-center">
            <div>
              <div className="text-xs text-zinc-500">DET</div>
              <div className="text-lg font-semibold">{formatPct(rqa?.rqa?.det)}</div>
            </div>
            <div>
              <div className="text-xs text-zinc-500">LAM</div>
              <div className="text-lg font-semibold">{formatPct(rqa?.rqa?.lam)}</div>
            </div>
            <div>
              <div className="text-xs text-zinc-500">TT</div>
              <div className="text-lg font-semibold">{rqa?.rqa?.tt?.toFixed(1) ?? "--"}</div>
            </div>
          </div>
        </div>

        <div className="col-span-12 lg:col-span-8 space-y-4">
          <div className="rounded-2xl border border-zinc-800 bg-zinc-950/40 p-5">
            <div className="mb-3 flex flex-wrap items-center justify-between gap-3">
              <div className="text-xs text-zinc-500">Preco medio (R$) com bandas de regime</div>
              <div className="flex items-center gap-2 text-xs text-zinc-400">
                <label>Visao</label>
                <select
                  className="rounded-lg border border-zinc-700 bg-black/30 px-2 py-1 text-xs"
                  value={viewMode}
                  onChange={(e) => setViewMode(e.target.value as "mensal" | "anual" | "diario")}
                >
                  <option value="mensal">Mensal</option>
                  <option value="anual">Anual</option>
                  <option value="diario">Diario</option>
                </select>
              </div>
            </div>

            {chart ? (
              <div className="relative">
                <svg
                  width={chart.width}
                  height={chart.height}
                  className="w-full"
                  onMouseLeave={() => setHoverIndex(null)}
                  onMouseMove={(e) => {
                    const rect = (e.currentTarget as SVGSVGElement).getBoundingClientRect();
                    const x = e.clientX - rect.left;
                    const idx = Math.round(
                      ((x - chart.pad) / Math.max(1, rect.width - chart.pad * 2)) * (displaySeries.length - 1)
                    );
                    setHoverIndex(idx >= 0 && idx < displaySeries.length ? idx : null);
                  }}
                >
                  <rect x={0} y={0} width={chart.width} height={chart.height} fill="transparent" />

                  {activeRegimes.length > 1 &&
                    activeRegimes.map((r, i) => {
                      if (i === activeRegimes.length - 1) return null;
                      const x0 = chart.scaleX(i, activeRegimes.length);
                      const x1 = chart.scaleX(i + 1, activeRegimes.length);
                      return (
                        <rect
                          key={`${r.date}-${i}`}
                          x={x0}
                          y={chart.pad}
                          width={x1 - x0}
                          height={chart.height - chart.pad * 2}
                          fill={palette[r.regime] || "#3f3f46"}
                          opacity={0.12}
                        />
                      );
                    })}

                  <polyline
                    fill="none"
                    stroke="#38bdf8"
                    strokeWidth={2.2}
                    points={displaySeries
                      .map((p, i) => (p.value == null ? null : `${chart.scaleX(i, displaySeries.length)},${chart.scaleY(p.value)}`))
                      .filter(Boolean)
                      .join(" ")}
                  />

                  {[0, 0.25, 0.5, 0.75, 1].map((p) => {
                    const y = chart.pad + (1 - p) * (chart.height - chart.pad * 2);
                    const value = chart.ymin + p * (chart.ymax - chart.ymin);
                    return (
                      <g key={`y-${p}`}>
                        <line x1={chart.pad} y1={y} x2={chart.width - chart.pad} y2={y} stroke="#1f2937" strokeDasharray="4 6" />
                        <text x={chart.pad - 8} y={y + 4} fill="#9ca3af" fontSize="10" textAnchor="end">
                          {value.toFixed(0)}
                        </text>
                      </g>
                    );
                  })}

                  {[0, 0.25, 0.5, 0.75, 1].map((p) => {
                    const idx = Math.round(p * (displaySeries.length - 1));
                    const x = chart.scaleX(idx, displaySeries.length);
                    return (
                      <text key={`x-${p}`} x={x} y={chart.height - chart.pad + 18} fill="#9ca3af" fontSize="10" textAnchor="middle">
                        {formatDateLabel(displaySeries[idx]?.date ?? "", viewMode)}
                      </text>
                    );
                  })}
                </svg>

                {hoverIndex != null && displaySeries[hoverIndex] && (
                  <div className="absolute right-6 top-6 rounded-lg border border-zinc-700 bg-black/80 px-3 py-2 text-xs text-zinc-200">
                    <div className="font-semibold">{formatDateLabel(displaySeries[hoverIndex].date, viewMode)}</div>
                    <div>Preco: R$ {displaySeries[hoverIndex].value?.toFixed(0) ?? "--"}</div>
                    {regimeAt(hoverIndex) && (
                      <div>
                        Regime: {toTitleRegime(regimeAt(hoverIndex)?.regime)} | Conf. {regimeAt(hoverIndex)?.confidence?.toFixed(2) ?? "--"}
                      </div>
                    )}
                  </div>
                )}
              </div>
            ) : (
              <div className="text-sm text-zinc-400">Sem dados carregados.</div>
            )}
          </div>

          <div className="grid gap-4 lg:grid-cols-2">
            <div className="rounded-xl border border-zinc-800 bg-black/40 p-4">
              <div className="text-xs text-zinc-500 mb-2">Resumo por ativo</div>
              <div className="grid grid-cols-2 gap-2 text-sm text-zinc-200">
                <div className="text-zinc-400">Ativo</div>
                <div>{selectedAsset}</div>
                <div className="text-zinc-400">Periodo analisado</div>
                <div>{periodInfo}</div>
                <div className="text-zinc-400">Regime atual</div>
                <div>{toTitleRegime(regimeNow?.regime)}</div>
                <div className="text-zinc-400">Confianca</div>
                <div>{regimeNow?.confidence?.toFixed(2) ?? "--"}</div>
              </div>
            </div>

            <div className="rounded-xl border border-zinc-800 bg-black/40 p-4">
              <div className="mb-2 flex items-center justify-between">
                <div className="text-xs text-zinc-500">Forecast e diagnostico</div>
                <select
                  className="rounded-lg border border-zinc-700 bg-black/30 px-2 py-1 text-xs"
                  value={horizonDays}
                  onChange={(e) => setHorizonDays(Number(e.target.value) as 1 | 5 | 10)}
                >
                  <option value={1}>1 dia</option>
                  <option value={5}>5 dias</option>
                  <option value={10}>10 dias</option>
                </select>
              </div>
              <div className="space-y-1 text-sm text-zinc-200">
                <div>Projecao p50 ({horizonDays}d): R$ {forecastValue?.p50?.toFixed(0) ?? "--"}</div>
                <div>Confianca da projecao: {forecastValue?.confidence?.toFixed(2) ?? "--"}</div>
                <div className="text-zinc-400">Use como diagnostico de regime, nao como sinal isolado.</div>
              </div>
              <div className="mt-3 text-xs text-zinc-400">
                {forecast
                  ? `${Object.keys(forecast).length} horizontes no resumo por regime do motor.`
                  : "Resumo de forecast por regime indisponivel (fallback local ativo)."}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
